import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Checkbox } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
const { TextArea } = Input;
export class DebtsForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //项目
                "key": "", // 编号
                "title": "", //标题
                "category": "", //类别
                "type": "", //类型
                "detail": "",   //详情
                "accessory": {        //附件
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //融资方
                "industry": "", //行业
                "aptitude": "", //资质
                "parentAptitue": "", //母公司资质
                "region": [], //地区
                "previousProfits": "", //去年净利润
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //金额
                "deadline": "", //期限
                "maxRate": "" //最高可接受利率
            },
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "name": "",  //抵押物名称
                "value": "",  //抵押物价值
                "rate": "" //抵押物折扣率
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "stock": {
                "type": "",  //参股性:1   控股型:2
                "property": "",  //老股转让:1   新股增发:2
                "number": "",  //转让金额
                "rate": "", //出让股权比例
                "value": "", //企业整体估值
                "estimate": {  //估值
                    "times": "",
                    "type": ""
                }
            },
            "subject": {  //标的企业
                "name": "",
                "industry": "",
                "region": "",
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //资产
                "region": "",
                "number": "",
                "value": ""
            },
            "audit": {
                "status": "",
                "comment": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/project/normal/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/project/normal/audit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("审核成功!", 1, function() {
                                hashHistory.push("/project/normal")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目标题"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{require: true, message: "请输入标题!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.props.actionType=="detail" ? true : false} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类别"
                    >
                        <Select disabled defaultValue="1">
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="4">资金转让</Option>
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类型"
                    >
                        {getFieldDecorator("project.type", {
                            initialValue: this.state.project.type
                        })(
                            <Select disabled>
                                {
                                    this.state.assetsType.debts.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方名称"
                    >
                        <div>详见我的联系方式</div>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方所属行业"
                    >
                        {getFieldDecorator("industry", {
                            initialValue: this.state.financing.industry
                        })(
                            <Select disabled>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    {
                        this.state.financing.industry == 1 ?
                        <FormItem
                            required
                            {...formItemLayout}
                            label="融资方开发资质"
                        >
                            <Input disabled defaultValue={this.state.financing.aptitude} />
                        </FormItem> : ""
                    }
                    {
                        this.state.financing.industry == 1 ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label="母公司开发资质"
                            >
                                <Input disabled defaultValue={this.state.financing.parentAptitue} />
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方所在地"
                    >
                        <Cascader disabled
                            options={this.state.cascaderRegion} value={this.state.financing.region}
                        />
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方去年净利润"
                    >
                        <Input style={{width: "95%"}} disabled value={this.state.financing.previousProfits} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资金额"
                    >
                        <Input style={{width: "95%"}} disabled value={this.state.financing.number} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="期限"
                    >
                        <Input style={{width: "95%"}} disabled value={this.state.financing.deadline} />&nbsp;年
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="最高接受利率"
                    >
                        <Input style={{width: "95%"}} disabled value={this.state.financing.maxRate} />&nbsp;%
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="有无抵/质押物"
                    >
                        <RadioGroup disabled value={this.state.mortgage.isNeed}>
                            <Radio value="1">是</Radio>
                            <Radio value="0">否</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="抵/质押物类型"
                    >
                        <CheckboxGroup disabled value={this.state.mortgage.type}>
                            <Checkbox value="1">固定资产</Checkbox>
                            <Checkbox value="2">有价证券</Checkbox>
                            <Checkbox value="3">其他</Checkbox>
                        </CheckboxGroup>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="抵/质押物名称"
                    >
                        <Input disabled value={this.state.mortgage.name} />
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="抵/质押物价值"
                    >
                        <Input style={{width: "95%"}} disabled value={this.state.mortgage.value} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="抵/质押物折扣率"
                    >
                        <Input style={{width: "95%"}} disabled value={this.state.mortgage.rate} />&nbsp;%
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="有无担保"
                    >
                        <RadioGroup disabled value={this.state.guarantee.isNeed}>
                            <Radio value="1">是</Radio>
                            <Radio value="0">否</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="担保方式"
                    >
                        <Input disabled value={this.state.guarantee.method} />
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="项目详情"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "请输入资金详情!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.props.actionType == "detail" ? true : false} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.project.accessory.link}>{this.state.project.accessory.name}</a>
                        </span>
                    </FormItem>
                    {
                        this.props.actionType == "audit" ?
                        <FormItem
                            required
                            {...formItemLayout}
                            label="审核状态"
                            hasFeedback={this.props.actionType=="audit" ? true : false}
                        >
                            {getFieldDecorator("audit.status", {
                                rules: [{required: true,message:"请选择审核状态!"}],
                                initialValue: this.state.audit.status
                            })(
                                <RadioGroup>
                                    <Radio value="0">不通过</Radio>
                                    <Radio value="1">通过</Radio>
                                </RadioGroup>
                            )}
                        </FormItem> : ""
                    }
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                {...formItemLayout}
                                label="审核意见"
                            >
                                {getFieldDecorator("audit.comment", {
                                    initialValue: this.state.audit.comment
                                })(
                                    <TextArea autosize={{ minRows: 3 }} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            this.props.actionType == "audit" ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""
                        }
                        {
                            this.props.actionType == "audit" ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDebtsForm = Form.create({})(DebtsForm);

module.exports = WrappedDebtsForm;


