import React,{Component} from 'react'
import { Input } from 'antd';
class InvestItem extends React.Component {
    constructor(props) {
        super(props);

        const value = this.props.value || {};
        this.state = {
            min: value.min || 0,
            max: value.max || 0
        };
        ['handleMinChange', 'handleMaxChange', 'triggerChange'].forEach(m=>this[m] = this[m].bind(this))
    }
    componentWillReceiveProps(nextProps) {
        // Should be a controlled component.
        if ('value' in nextProps) {
            const value = nextProps.value;
            this.setState(value);
        }
    }

    handleMinChange(e) {
        const min = e.target.value;
        if (!('value' in this.props)) {
            this.setState({ min });
        }
        this.triggerChange({ min });
    }

    handleMaxChange(e) {
        const max = parseInt(e.target.value || 0, 10);
        if (!('value' in this.props)) {
            this.setState({ max });
        }
        this.triggerChange({ max });
    }

    triggerChange (changedValue) {
        // Should provide an event to pass value to Form.
        const onChange = this.props.onChange;
        if (onChange) {
            onChange(Object.assign({}, this.state, changedValue));
        }
    }

    render() {
        return (
            <span>
                <Input type="text" value={this.state.min} onChange={this.handleMinChange} style={{ width: '20%' }} disabled />&nbsp;&nbsp;——&nbsp;&nbsp;
                <Input type="text" value={this.state.max} onChange={this.handleMaxChange} style={{ width: '20%' }} disabled />万元
            </span>
        );
    }
}

module.exports = InvestItem;


