import React,{Component} from 'react'
import { Input, Select, Icon } from 'antd';
const Option = Select.Option;

class BenefitsItem extends React.Component {
    constructor(props) {
        super(props);

        const value = this.props.value || {};
        this.state = {
            times: value.times || 0,
            type: value.type || "1"
        };
        ['handleTimesChange', 'handleTypeChange', 'triggerChange'].forEach(m=>this[m] = this[m].bind(this))
    }
    componentWillReceiveProps(nextProps) {
        // Should be a controlled component.
        if ('value' in nextProps) {
            const value = nextProps.value;
            this.setState(value);
        }
    }

    handleTimesChange(e) {
        const times = parseInt(e.target.value || 0, 10);
        if (!('value' in this.props)) {
            this.setState({ times });
        }
        this.triggerChange({ times });
    }

    handleTypeChange(type) {
        if (!('value' in this.props)) {
            this.setState({ type });
        }
        this.triggerChange({ type });
    }

    triggerChange(changedValue) {
        // Should provide an event to pass value to Form.
        const onChange = this.props.onChange;
        if (onChange) {
            onChange(Object.assign({}, this.state, changedValue));
        }
    }
    render() {
        return (
            <span>
        <Input
            disabled
            type="text"
            value={this.state.times}
            onChange={this.handleTimesChange}
            style={{ width: '25%' }}
        />&nbsp;倍&nbsp;<Icon type="close" />
        <Select disabled
            value={this.state.type}
            style={{ width: '25%' }}
            onChange={this.handleTypeChange}
        >
            <Option value="1">去年净利润</Option>
            <Option value="2">今年预计净利润</Option>
            <Option value="3">明年预计净利润</Option>

        </Select>
      </span>
        );
    }
}

module.exports = BenefitsItem;


