import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb } from 'antd';
const { Content } = Layout;
import DebtsForm from './Form/debtsForm.jsx'
import StockForm from './Form/stockForm.jsx'
import TransferForm from './Form/transferForm.jsx'

export class Audit extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    {
                        this.props.location.query.assetsType == 1 ? <DebtsForm infoID={this.props.params.id} actionType="audit" /> : (this.props.location.query.assetsType == 2 ? <StockForm infoID={this.props.params.id} actionType="audit" /> : <TransferForm infoID={this.props.params.id} actionType="audit" />)
                    }
                </Content>
            </Layout>
        )
    }
}

module.exports = Audit;


