import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import BenefitsItem from '../FormItem/benefitsItem.jsx'

export class StockForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //项目
                "key": "", // 编号
                "title": "", //标题
                "category": "", //类别
                "type": "", //类型
                "detail": "",   //详情
                "accessory": {        //附件
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //融资方
                "industry": "", //行业
                "aptitude": "", //资质
                "parentAptitued": "", //母公司资质
                "region": [], //地区
                "previousProfits": "", //去年净利润
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //金额
                "deadline": "", //期限
                "maxRate": "" //最高可接受利率
            },
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "name": "",  //抵押物名称
                "value": "",  //抵押物价值
                "rate": "" //抵押物折扣率
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "stock": {
                "type": "",  //参股性:1   控股型:2
                "property": "",  //老股转让:1   新股增发:2
                "number": "",  //转让金额
                "rate": "", //出让股权比例
                "estimate": {  //估值
                    "times": "",
                    "type": ""
                }
            },
            "target": {  //标的企业
                "name": "",
                "industry": "",
                "region": "",
                "value": "", //企业整体估值
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //资产
                "region": "",
                "number": "",
                "value": ""
            },
            "audit": {
                "status": "",
                "comment": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/project/normal/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/project/normal/audit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("审核成功!", 1, function() {
                                hashHistory.push("/project/normal")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目标题"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{require: true, message: "请输入标题!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.props.actionType=="detail" ? true : false} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类别"
                    >
                        <Select disabled defaultValue="2">
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="4">资金转让</Option>
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="参/控股"
                    >
                        <RadioGroup disabled value={this.state.stock.type}>
                            <Radio value="1">参股型项目</Radio>
                            <Radio value="2">控股型项目</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类型"
                    >
                        {getFieldDecorator("project.type", {
                            initialValue: this.state.project.type
                        })(
                            <Select disabled>
                                {
                                    this.state.assetsType.stock.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="股权性质"
                    >
                        <Select disabled value={this.state.stock.property}>
                            <Option value="1">老股转让</Option>
                            <Option value="2">新股增发</Option>
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "转让方名称" : "融资方名称"}
                    >
                        <div>详见我的联系方式</div>
                    </FormItem>
                    {
                        this.state.stock.property == "1" ? <FormItem
                            required
                            {...formItemLayout}
                            label= "标的企业名称"
                        >
                            <Input disabled value={this.state.target.name} />
                        </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "标的企业所属行业" : "融资方所属行业"}
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.industry" : "financing.industry", {
                            initialValue: this.state.stock.property == "1" ? this.state.target.industry : this.state.financing.industry
                        })(
                            <Select disabled>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "标的企业所在地" : "融资方所在地"}
                    >
                        <Cascader disabled
                                  options={this.state.cascaderRegion} value={this.state.stock.property == "1" ? this.state.target.region : this.state.financing.region}
                        />
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "标的企业去年利润" : "融资方去年利润"}
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.previousProfits : this.state.financing.previousProfits} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "转让金额" : "融资金额"}
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.number} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="出让股权比例"
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.rate} />&nbsp;%
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="企业整体估值"
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.number : this.state.financing.number} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="投资估值倍数"
                    >

                        {getFieldDecorator("stock.estimate", {
                            initialValue: {
                                times: this.state.stock.estimate.times,
                                type: this.state.stock.estimate.type
                            }
                        })(<BenefitsItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="企业今年预计净利润"
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.recentProfits : this.state.financing.recentProfits} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="企业明年预计净利润"
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.nextProfits: this.state.financing.nextProfits} />&nbsp;万元
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="项目详情"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "请输入资金详情!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.props.actionType == "detail" ? true : false} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="项目附件"
                    >
                        <span className="ant-form-text"><a href={this.state.project.accessory.link}>{this.state.project.accessory.name}</a>
                        </span>
                    </FormItem>
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label="审核状态"
                                hasFeedback={this.props.actionType=="audit" ? true : false}
                            >
                                {getFieldDecorator("audit.status", {
                                    rules: [{required: true,message:"请选择审核状态!"}],
                                    initialValue: this.state.audit.status
                                })(
                                    <RadioGroup>
                                        <Radio value="0">不通过</Radio>
                                        <Radio value="1">通过</Radio>
                                    </RadioGroup>
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                {...formItemLayout}
                                label="审核意见"
                            >
                                {getFieldDecorator("audit.comment", {
                                    initialValue: this.state.audit.comment
                                })(
                                    <TextArea autosize={{ minRows: 3 }} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            this.props.actionType == "audit" ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""
                        }
                        {
                            this.props.actionType == "audit" ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedStockForm = Form.create({})(StockForm);

module.exports = WrappedStockForm;


